import { Box, Card, CardActionArea, Grid, Typography } from "@material-ui/core"
import { makeStyles } from "@material-ui/core/styles"
import { navigate } from "gatsby"
import Img from "gatsby-image"
import React from "react"
import grey from "@material-ui/core/colors/grey"
import { PostType } from "../templates/PostListByCategoryTemplate"

type Props = {
  post: PostType
}

const PostCard: React.FC<Props> = ({ post }) => {
  const classes = useStyles()
  const title = post.frontmatter.title || post.fields.slug

  return (
    <Card
      variant="outlined"
      onClick={() => setTimeout(() => navigate(post.fields.slug), 300)}
      className={classes.postItemCard}
    >
      <CardActionArea className={classes.postItemCardArea}>
        <Box className={classes.postItemLayout}>
          <Box className={classes.thumbnailBox}>
            {post.frontmatter.thumbnail !== null && (
              <Img
                fluid={post.frontmatter.thumbnail.childImageSharp.fluid}
                className={classes.thumbnail}
              />
            )}
          </Box>
          <Box className={classes.allTextBox}>
            <Typography variant="h3" className={classes.titleText}>
              {title}
            </Typography>
            <Typography className={classes.dateText} variant="subtitle2">
              {post.frontmatter.date}
            </Typography>
            <span
              className={classes.decriptionText}
              dangerouslySetInnerHTML={{
                __html: post.frontmatter.description || post.excerpt,
              }}
              itemProp="description"
            />
          </Box>
        </Box>
      </CardActionArea>
    </Card>
  )
}

export default PostCard

const useStyles = makeStyles(theme => ({
  postItemCardArea: {},
  postItemCard: {
    margin: theme.spacing(1, 0, 0, 0),
    borderRadius: "10px",
    boxShadow: "rgba(116, 129, 141, 0.1) 0px 3px 8px 0px;",
  },
  postItemLayout: {
    height: "400px",
    display: "flex",
    flexDirection: "column",
  },
  thumbnailBox: {
    height: "200px",
  },
  allTextBox: {
    flex: "1",
    display: "flex",
    flexDirection: "column",
    alignContent: "center",
    padding: theme.spacing(0, 3),
    overflow: "hidden",
    justifyContent: "center",
  },
  thumbnail: {
    height: "100%",
  },
  titleText: {
    fontSize: "18px",
    margin: theme.spacing(0, 0, 0.2, 0),
  },
  dateText: {
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis",
    color: grey[600],
    fontSize: "12px",
  },
  decriptionText: {
    ...theme.typography.body1,
    padding: theme.spacing(1, 0, 0, 0),
    fontSize: "14px",
    lineHeight: "1.5",
    display: "-webkit-box",
    overflow: "hidden",
    textOverflow: "ellipsis",
    "-webkit-line-clamp": 5,
    "-webkit-box-orient": "vertical",
  },
}))
