import { Card, CardActionArea, Grid, Typography } from "@material-ui/core"
import grey from "@material-ui/core/colors/grey"
import { makeStyles } from "@material-ui/core/styles"
import { navigate } from "gatsby"
import Img from "gatsby-image"
import React from "react"
import { PostType } from "../templates/PostListByCategoryTemplate"

type Props = {
  post: PostType
}

const PostListItem: React.FC<Props> = ({ post }) => {
  const classes = useStyles()
  const title = post.frontmatter.title || post.fields.slug

  return (
    <Card
      variant="outlined"
      onClick={() => setTimeout(() => navigate(post.fields.slug), 300)}
      className={classes.postItemCard}
    >
      <CardActionArea className={classes.postItemCardArea}>
        <Grid container className={classes.postItemLayout}>
          <Grid item xs={3} className={classes.thumbnailBox}>
            {post.frontmatter.thumbnail !== null && (
              <Img
                fluid={post.frontmatter.thumbnail.childImageSharp.fluid}
                className={`${classes.thumbnail} thumbnail`}
              />
            )}
          </Grid>
          <Grid item xs={9} className={classes.allTextBox}>
            <Typography variant="h3" className={classes.titleText}>
              {title}
            </Typography>
            <Typography className={classes.dateText} variant="subtitle2">
              {post.frontmatter.date}
            </Typography>
            <span
              className={classes.decriptionText}
              dangerouslySetInnerHTML={{
                __html: post.frontmatter.description || post.excerpt,
              }}
              itemProp="description"
            />
          </Grid>
        </Grid>
      </CardActionArea>
    </Card>
  )
}

export default PostListItem

const useStyles = makeStyles(theme => ({
  postItemCardArea: {
    "&:hover .thumbnail": {
      transform: "scale(1.1)",
    },
  },
  postItemCard: {
    margin: theme.spacing(2, 0, 0, 0),
    borderRadius: "10px",
    boxShadow: "rgba(116, 129, 141, 0.1) 0px 3px 8px 0px;",
  },
  titleBox: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    padding: theme.spacing(0, 0, 7, 0),
  },
  thumbnailBox: {
    height: "100%",
    overflow: "hidden",
  },
  allTextBox: {
    flex: "1",
    display: "flex",
    flexDirection: "column",
    alignContent: "center",
    padding: theme.spacing(3),
    overflow: "hidden",
    justifyContent: "center",
  },
  thumbnail: {
    height: "100%",
    transition: "transform 250ms 0s ease-in-out",
  },
  postItemLayout: {
    height: "180px",
    display: "flex",
    flexDirection: "row",
  },
  titleText: {
    margin: theme.spacing(0, 0, 0.5, 0),
  },
  dateText: {
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis",
    color: grey[600],
  },
  decriptionText: {
    ...theme.typography.body1,
    padding: theme.spacing(1, 0, 0, 0),
    fontSize: "16px",
    lineHeight: "1.5",
    display: "-webkit-box",
    overflow: "hidden",
    textOverflow: "ellipsis",
    "-webkit-line-clamp": 3,
    "-webkit-box-orient": "vertical",
  },
}))
